import React, { Component } from "react";
import { Form, Button} from 'react-bootstrap';
import OtherAuthAction from "./Components/OtherAuthAction";
import { forgotPassword } from '../../actions/loginActions'
import { validateEmail } from "../../utils";
import FormTerms from "./Components/FormTerms";
class ForgotPassword extends Component {
    constructor(props) {
        super(props)
    }
    state = {
        accountEmailId: null,
        emailError: null,
        emailErrorMessage: null,
        success: null,
        btnLoader: false,
    }
    onEmailTextChange = event => {
        this.setState({ accountEmailId: event.target.value, emailError: null, emailErrorMessage: null })
    }
    onClickToForgot = event => {
        const { accountEmailId } = this.state;
        const { activeProduct } = this.props
        var self = this;
        self.setState({ btnLoader: true });
        const commonContent = window?.APP_CONTENT?.["common"] ?? null;

        if (accountEmailId && String(accountEmailId).trim().length > 0 && validateEmail(accountEmailId)) {
            forgotPassword({ emailId: accountEmailId, activeProduct }).then((response) => {
                self.setState({ success: true, emailErrorMessage: null, btnLoader: false, })
            }).catch((ex) => {
                const { data } = ex.response
                self.setState({ emailError: true, emailErrorMessage: commonContent?.email_not_found ?? "", btnLoader: false, })
            })
        }
        else {
            this.setState({ emailError: true, btnLoader: false, emailErrorMessage: accountEmailId && String(accountEmailId).trim().length > 0 ? commonContent?.valid_email ?? "": commonContent?.req_email ?? ""})
        }

    }
    render() {
        const { prefix } = this.props
        const { accountEmailId, emailError, success, emailErrorMessage, btnLoader } = this.state
        const pageContent = window?.APP_CONTENT?.["forgot_pass"] ?? null;
        const commonContent = window?.APP_CONTENT?.["common"] ?? null;
        return <div className="w-100 mb-lg-0 mb-3">
            <div className="text-start mb-5">
                <h1 className="mb-0 display-2 fw-bold mb-3">{pageContent?.welcome_msg ?? ""}</h1>
                <div className="text-gray-500 fs-8">{pageContent?.enter_email ?? ""}</div>
            </div>
            {success ? <div className="alert alert-success py-2" role="alert">
                {pageContent?.success_msg ?? ""}
            </div>: null}
            <div className="mb-8">
                <Form.Group className="mb-4 mb-xl-5">
                    <Form.Label className="fw-bold" htmlFor="email_">{commonContent?.email ?? ""}</Form.Label>
                    <Form.Control type="email" size="lg" placeholder="you@yourcompany.com" className={`${emailError ? `is-invalid` : ``}`} value={accountEmailId || ''} onChange={this.onEmailTextChange}  />
                    {emailError ? <span className="invalid-feedback">{emailErrorMessage}</span> : null}
                </Form.Group>
            </div>
            <div className="d-grid mb-4 w-100">
                <Button variant="primary" size={'lg'} type="submit" className={`${btnLoader ? ' disabled' : ''}`}  onClick={this.onClickToForgot}>
                    <span className="indicator-label" style={{ display: btnLoader ? 'none' : 'block' }}>{commonContent?.reset_password ?? ""}</span>
                    <span className="indicator-progress" style={{ display: !btnLoader ? 'none' : 'block' }}>{commonContent?.wait ?? ""}
                        <span className="spinner-border spinner-border-sm align-middle ms-2"></span>
                    </span>
                </Button>
            </div>
            <OtherAuthAction text={commonContent?.back_to ?? ""} link={'/accounts/login'} linkText={commonContent?.login ?? ""} />
            <FormTerms/>
        </div>
    }
}

export default ForgotPassword